import React from 'react';
import { withStyles } from '@material-ui/core/styles';

/** Components **/
import { Tabs, Tab, AppBar } from '@material-ui/core';
import BrandGallery from './brand-gallery/BrandGallery';
import AddPortfolio from './add-portfolio/AddPortfolio';
import Insights from './insights/Insights';

/** Constants **/
import ROUTING_PATHS from '../../constants/routes';
import {HOME_PAGE} from '../../constants/properties'

const HomeTabs = withStyles(
    () => {
        return {
            root: {
                margin: '0 auto 4px'
                //marginBottom: '4px'
            },
            indicator: {
                backgroundColor: 'white'

            }
        }
    }
)(Tabs);

const HOME_STYLE = theme => ({
    //some style here.
    wrapper: {
        //background: 'red'
    }
})

class Home extends React.Component {
    // constructor(props) {
    //     super();
    // }

    state = {   // build as a class props
        path: ROUTING_PATHS.BRAND_GALLERY
    }
    componentDidMount() {
        this.setState({path: this.props.location.pathname});
    }
    // static getDerivedStateFromProps(props, state) {
    //     state.path=props.location.pathname;
    //     return null;
    // }

    updateOnTabClick(itempath) {
        this.setState({ path: itempath });
        // need to update the route on browser
        if (this.props.location.pathname!==itempath) {
            this.props.history.push(itempath);
        }
    }
    
    get HomeAppBar() {
        const PageTabs = [];
        for(let i=0; i<HOME_PAGE.tabsInfo.length; i++){
            const tabInfo = HOME_PAGE.tabsInfo[i]
            PageTabs.push(
                <Tab
                    key = {i}
                    label={tabInfo.label}
                    value={tabInfo.path}
                    onClick={() => this.updateOnTabClick(tabInfo.path)} />
            )
        }
        return (
            <AppBar position="static" elevation={0}>
                <HomeTabs
                    //indicatorColor="primary"
                    //textColor="primary"
                    aria-label="disabled tabs example"
                    variant="fullWidth"
                    value={this.state.path}
                >
                    {PageTabs}
                </HomeTabs>
            </AppBar>
        )
    }

    get HomeBody() {
        switch (this.state.path) {
            case ROUTING_PATHS.BRAND_GALLERY:
                return <BrandGallery />;
            case ROUTING_PATHS.ADD_PORTFOLIO:
                return <AddPortfolio />;
            case ROUTING_PATHS.INSIGHTS:
                return <Insights />;
            default:
                return null;
        }
    }

    render() {
        const classes = this.props.classes; // get from withStyles function
        return (
            <div className={classes.wrapper}>
                {this.HomeAppBar}
                {this.HomeBody}
            </div>
        )
    }
}

export default withStyles(HOME_STYLE)(Home);