const defaultState = Object.freeze({
    brands: [],
    brand: {},
    productData: [],
    industryData: [],
    countryData:[],
})

function brandReducer(state = defaultState, action) {
    switch (action.type) {
        case 'UPDATE_BRANDS':
            const newBrands = action.data;
            // prepare product data
            const productCountObj = {};
            for(const brand of newBrands){
                for(const product of brand.products) {
                    productCountObj[product] = productCountObj[product] + 1 || 1;
                }
            }
            const productCountData = [['Product Name', 'Count']];
            for(const productName of Object.keys(productCountObj)){
                productCountData.push([productName, productCountObj[productName]]);
            }
            // Prepare industry data
            const industryCountObj = {};
            for(const brand of newBrands){
                for(const industry of brand.industries){
                    industryCountObj[industry] = industryCountObj[industry] + 1 || 1;
                }
            }
            const industryCountData = [['Industry Name', 'Count']];
            for(const industryName of Object.keys(industryCountObj)){
                industryCountData.push([industryName, industryCountObj[industryName]]);
            }
            // Prepare country data
            const countryCountObj = {};
            for(const brand of newBrands){
                for(const country of brand.headquarter_countries){
                    countryCountObj[country] = countryCountObj[country] + 1 || 1;
                }
            }
            const countryCountData = [['Country Name', 'Count']];
            for(const countryName of Object.keys(countryCountObj)){
                countryCountData.push([countryName, countryCountObj[countryName]]);
            }

            return Object.assign({}, state, {   //step 9
                brands: newBrands,
                productData: productCountData,
                industryData: industryCountData,    // update industry and country data
                countryData: countryCountData
            });
        case 'UPDATE_BRAND':
            const newBrand = action.data;
            return Object.assign({}, state, {
                brand: newBrand
            });
        default:
            return state;
    }
}
export default brandReducer;