import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { withStyles, withWidth, Chip, Card, Button, TextField, Grid, Select, MenuItem, Input, FormControl, InputLabel, Typography } from '@material-ui/core';
import { CLOUDINARY } from '../../../constants/secret';
import ChipInput from 'material-ui-chip-input';
import { INDUSTRIES, PRODUCTS, HEADQUARTERCOUNTRIES } from '../../../constants/properties';
import { addBrand } from '../../../dispatchers/dispatcher';

const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: 48 * 4.5 + 8,
            width: 250
        }
    }
};
const STYLE = () => ({
    chips: {
        display: "flex",
        flexWrap: "wrap"
    },
    chip: {
        margin: 4
    }
});

class AddPortfolio extends React.Component {
    state = {
        name: '',
        website: '',
        logoUrl: '',
        industries: [],
        headquarterCountries: [],
        products: [],
        foundedYear: '',
        keyPeople: [],
        description: '',
        mission: ''
    }

    VerticalGap(heightPx) {
        return <div style={{ height: heightPx, width: '1px' }} />;
    }

    get YearOptions() {
        const yearOptions = [];
        const currentYear = new Date().getFullYear();
        for (let year = currentYear - 200; year <= currentYear; year++) {
            yearOptions.push(year);
        }
        return yearOptions;
    }

    addPeople(person) {
        const people = this.state.keyPeople;
        people.push(person);
        this.setState({ keyPeople: people });
    }

    removePeople(person, index) {
        const people = this.state.keyPeople;
        people.splice(index, 1);
        this.setState({ keyPeople: people });
    }

    updateForm(event, stateName) {
        event.preventDefault();
        const newValue = event.target.value;
        this.setState({ [stateName]: newValue });
    }

    submitForm(event) {
        event.preventDefault();
        const brandForm = {
            logoUrl: this.state.logoUrl,
            name: this.state.name,
            website: this.state.website,
            foundedYear: this.state.foundedYear,
            headquarterCountries: this.state.headquarterCountries,
            keyPeople: this.state.keyPeople,
            industries: this.state.industries,
            products: this.state.products,
            description: this.state.description,
            mission: this.state.mission
        };
        this.props.propsAddBrand(brandForm).then(brandId => {
            this.props.history.push(`/brand/${brandId}`);
        });
    }


    setMockData() {
        this.setState({
            logoUrl: 'https://res.cloudinary.com/helensqj/image/upload/v1567451901/dpntkbsbhqtymns2xrfh.png',
            name: 'Glogou',
            website: 'https://www.glogou.com',
            foundedYear: 2015,
            headquarterCountries: ['U.S.'],
            keyPeople: ['Tom', 'James', 'Michael'],
            industries: ['Agency'],
            products: ['Advertisment'],
            description: 'We are a great company!',
            mission: 'Srtive to build the best product'
        });
    }

    openImageWidget(event) {
        event.preventDefault();
        window.cloudinary.openUploadWidget({
            cloud_name: CLOUDINARY.CLOUD_NAME,
            upload_preset: CLOUDINARY.UPLOAD_PRESET,
            theme: 'minimal'
        }, (errors, response) => {
            if (!errors) {
                this.setState({ logoUrl: response[0].url })
            }
        });
    }
    // window.cloudinary.openUploadWidget=function(e,t){return s(e,t).open()}

    get LogoArea() {
        const logoUrl = this.state.logoUrl;
        if (!logoUrl) {
            return (
                <div style={{
                    height: '160px', width: '100%', border: '2px dashed #ccc',
                    borderRadius: '6px',
                    display: 'flex', justifyContent: 'center', alignItems: 'center',
                    cursor: 'pointer'
                }} onClick={event => this.openImageWidget(event)}>
                    <Typography variant='h6' style={{ color: '#999' }}>
                        Upload brand logo
                    </Typography>
                </div>
            );
        } else {
            return (
                <div style={{
                    height: '160px', width: '100%',
                    display: 'flex', justifyContent: 'center', alignItems: 'center',
                    cursor: 'pointer'
                }} onClick={event => this.openImageWidget(event)}>
                    <img src={this.state.logoUrl} alt='logo'
                        style={{ height: '100%' }} />
                </div>
            );
        }
    }

    getStyles(stateName, keyValue) {
        if (stateName === 'foundedYear') {
            if (this.state[stateName] === keyValue) {
                return { fontWeight: 'bold' };
            } 
            // else {
            //     return {};
            // }
        }else{
        if (this.state[stateName].includes(keyValue)) {
            return { fontWeight: 'bold' };
        }}

        // return stateName === 'foundedYear' ? 
        //     this.state[stateName] === keyValue ? 
        //         { fontWeight: 'bold' }: {} :
        //     (stateName !== 'foundedYear' && this.state[stateName].includes(keyValue)) ? 
        //     { fontWeight: 'bold' }: {};
        // return {fontWeight: 400};
    }

    get InputInformation() {
        const css = this.props.classes;
        return (
            <Card>
                <Grid container direction='column' style={{ padding: '20px' }}>
                    {this.LogoArea}
                    {this.VerticalGap('12px')}
                    <TextField
                        label='Brand Name'
                        value={this.state.name}
                        onChange={event => this.updateForm(event, 'name')}
                        margin='normal'
                        fullWidth />
                    {/* {this.VerticalGap('8px')} */}
                    <TextField
                        label='Website'
                        value={this.state.website}
                        onChange={event => this.updateForm(event, 'website')}
                        margin='normal'
                        fullWidth />
                    {this.VerticalGap('18px')}
                    <FormControl>
                        <InputLabel>Founded Year</InputLabel>
                        <Select label='Founded Year'
                            value={this.state.foundedYear}
                            input={<Input />}
                            MenuProps={MenuProps}
                            onChange={event => this.updateForm(event, 'foundedYear')}>
                            {this.YearOptions.map((year) => (
                                <MenuItem key={year} value={year} style={this.getStyles('foundedYear', year)}>
                                    {year}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    {this.VerticalGap('25px')}
                    <FormControl>
                        <InputLabel>Headquarter Countries</InputLabel>
                        <Select multiple
                            label='Headquarter Countries'
                            value={this.state.headquarterCountries}
                            input={<Input />}
                            MenuProps={MenuProps}
                            onChange={event => this.updateForm(event, 'headquarterCountries')}
                            renderValue={selected => (
                                <div className={css.chips}>
                                    {selected.map(value => (
                                        <Chip key={value} label={value} className={css.chip} />
                                    ))}
                                </div>
                            )}>
                            {HEADQUARTERCOUNTRIES.map(headquarterCountries => (
                                <MenuItem key={headquarterCountries} value={headquarterCountries} style={this.getStyles('headquarterCountries', headquarterCountries)}>
                                    {headquarterCountries}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    {this.VerticalGap('19px')}
                    <ChipInput
                        label='Key People'
                        value={this.state.keyPeople}
                        onAdd={person => this.addPeople(person)}
                        onDelete={(person, index) => this.removePeople(person, index)}
                    />
                    {this.VerticalGap('27px')}
                    <FormControl>
                        <InputLabel>Industries</InputLabel>
                        <Select multiple
                            label='Industries'
                            value={this.state.industries}
                            input={<Input />}
                            MenuProps={MenuProps}
                            onChange={event => this.updateForm(event, 'industries')}
                            renderValue={selected => (
                                <div className={css.chips}>
                                    {selected.map(value => (
                                        <Chip key={value} label={value} className={css.chip} />
                                    ))}
                                </div>
                            )}>
                            {INDUSTRIES.map(industries => (
                                <MenuItem key={industries} value={industries} style={this.getStyles('industries', industries)}>
                                    {industries}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    {this.VerticalGap('25px')}
                    <FormControl>
                        <InputLabel>Products</InputLabel>
                        <Select multiple
                            label='Products'
                            value={this.state.products}
                            input={<Input id='select-products' />}
                            MenuProps={MenuProps}
                            onChange={event => this.updateForm(event, 'products')}
                            renderValue={selected => (
                                <div className={css.chips}>
                                    {selected.map(value => (
                                        <Chip key={value} label={value} className={css.chip} />
                                    ))}
                                </div>
                            )}>
                            {PRODUCTS.map(products => (
                                <MenuItem key={products} value={products} style={this.getStyles('products', products)}>
                                    {products}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    {this.VerticalGap('25px')}
                    <TextField style={{ marginTop: '10px' }}
                        label='Mission'
                        value={this.state.mission}
                        margin='normal'
                        variant='outlined'
                        onChange={event => this.updateForm(event, 'mission')}
                        fullWidth />
                    {this.VerticalGap('12px')}
                    <TextField
                        label='Description'
                        value={this.state.description}
                        margin='normal'
                        variant='outlined'
                        multiline
                        rows='4'
                        onChange={event => this.updateForm(event, 'description')}
                        fullWidth />
                    {this.VerticalGap('12px')}
                    <Grid container direction='row' justify='flex-end'>
                        <Button variant='outlined' size='medium' color='secondary'
                            onClick={() => this.setMockData()}>
                            Mock
                        </Button>
                        <Button variant='contained' size='medium' color='primary'
                            onClick={event => this.submitForm(event)}
                            style={{ marginLeft: '5px' }}>
                            Submit
                        </Button>
                    </Grid>
                </Grid>
            </Card>
        );
    }

    render() {
        const isXs = this.props.width === 'xs';
        const isSm = this.props.width === 'sm';
        let maxWidth = '800px';
        if (isSm) {
            maxWidth = '550px';
        } else if (isXs) {
            maxWidth = '360px';
        }

        return (
            <Grid container direction='column' style={{ padding: '48px 0' }}>
                <div style={{
                    display: 'flex', flexDirection: 'column', justifyContent: 'flex-start',
                    maxWidth: maxWidth,
                    padding: '16px',
                    margin: '0 auto', width: '100%'
                }}>
                    {this.InputInformation}
                </div>
            </Grid>
        );
    }
}

const mapStateToProps = () => {
    return {
    };
}

const mapDispatchToProps = dispatch => {
    return {
        propsAddBrand: brand => dispatch(addBrand(brand))
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(withWidth()(withStyles(STYLE)(AddPortfolio))));