import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { withWidth, Grid, Button, Icon } from '@material-ui/core';
import Chart from 'react-google-charts';
import { fetchBrands } from '../../../dispatchers/dispatcher';

class Insights extends React.Component {
    componentDidMount() {
        this.props.propsFetchBrands();
    }
    state = {
        display: 'product' // can be ['product', 'industry', 'country']
    }

    get Chart() {
        const industryData = this.props.industryData;
        const productData = this.props.productData;
        const countryData = this.props.countryData;
        let data = countryData;
        let title = '';
        let xTitle = '';
        if (this.state.display === 'product') {
            data = productData;
            title = 'product';
            xTitle = 'Products';
        } else if (this.state.display === 'industry') {
            data = industryData;
            title = 'industry';
            xTitle = 'Industries';
        } else {
            data = countryData;
            title = 'country';
            xTitle = 'Countries';
        }
        if (data.length === 0) {
            return '';
        }
        return (
            <div>
                <Chart
                    width={'1000px'}
                    height={'650px'}
                    style={{ margin: '0 auto' }}
                    chartType="ColumnChart"
                    loader={''}
                    data={data}
                    options={{
                        title: `Brand ${title} counts`,
                        chartArea: { width: '70%' },
                        hAxis: {
                            title: xTitle,
                            minValue: 0,
                        },
                        vAxis: {
                            title: 'Count',
                        },
                    }}
                />
            </div>
        );
    }

    setDisplay(chartName) {
        this.setState({ display: chartName });
    }

    get Control() {
        const buttons = [
            'product',
            'industry',
            'country'
        ]
        const controlBar = [];
        for (const buttonName of buttons) {
            controlBar.push(
                <Button key={buttonName} color="primary" fullWidth
                    onClick={() => this.setDisplay(buttonName)}>
                    <Grid container direction="row">
                        <Icon>
                            assessment
                        </Icon>
                        <span>
                            {buttonName}
                        </span>
                    </Grid>
                </Button>
            );
        }

        let marginTopValue = '180px';
        if (this.props.width === 'sm') {
            marginTopValue = '40px';
        }
        return (
            <Grid container direction="column" style={{ marginTop: marginTopValue }}>
                {controlBar}
            </Grid>
        );
    }

    render() {
        const isXs = this.props.width === 'xs';
        const isSm = this.props.width === 'sm';
        if (isXs) {
            return (<Grid>Please use large screen to view</Grid>);
        }

        if (isSm) {
            return (
                <Grid container direction='column' alignItems="center"
                    style={{ overflowY: 'hidden', overflowX: 'auto' }}>
                    <Grid item>
                        {this.Control}
                    </Grid>
                    <Grid item>
                        {this.Chart}
                    </Grid>
                </Grid>
            );
        }
        return (
            <Grid container direction='row' justify="center">
                <Grid item>
                    {this.Control}
                </Grid>
                <Grid item sm={9} style={{ overflowY: 'hidden', overflowX: 'auto' }}>
                    {this.Chart}
                </Grid>
            </Grid>
        );
    }
}

const mapStateToProps = state => {
    const productData = state.brandReducer.productData;
    const industryData = state.brandReducer.industryData;
    const countryData = state.brandReducer.countryData;
    // fetch industry and country data
    return {
        productData: productData.slice(),
        industryData: industryData.slice(),
        countryData: countryData.slice()
    };
}

const mapDispatchToProps = dispatch => {
    return {
        propsFetchBrands: () => dispatch(fetchBrands())
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(withWidth()(Insights)));