import React from 'react';
import { connect } from 'react-redux';
import { fetchBrands, fetchBrand } from '../../../dispatchers/dispatcher';
import { withRouter } from 'react-router';
import { withWidth, Card, CardContent, Grid, Typography, TextField, Button } from '@material-ui/core';
import { withStyles } from '@material-ui/styles';
//import './BrandGallery.css';

const BRANDS_PER_PAGE = 12;

const STYLE = () => ({
  card: {
    background: '#f5f5f5',
    cursor: 'pointer',
    '&:hover': {
      boxShadow: '0 3px 13px #666'
    }
  },
});

class BrandGallery extends React.Component {
  state = {
    viewBrands: [],
    currentPage: 1,
    firstPage: 1,
    lastPage: 0,
    keyword: ''
  }

  componentDidMount() {
    if (localStorage.getItem('currentPage')) {
      const currentPage = localStorage.getItem('currentPage');
      this.setState({ currentPage: Number(currentPage) });
    }
    //step 1
    this.props.propsFetchBrands().then(() => {
      const base = Math.floor(this.props.propsBrands.length / BRANDS_PER_PAGE);
      const carrier = this.props.propsBrands.length % BRANDS_PER_PAGE === 0 ? 0 : 1;
      this.setState({ lastPage: base + carrier })
      this.updateBrandsByPage(this.state.currentPage);
    });
  }
  updateBrandsByPage(page) {
    if (page < this.state.firstPage || page > this.state.lastPage) {
      return;
    }
    localStorage.setItem('currentPage', page);
    const startBrand = (page - 1) * BRANDS_PER_PAGE;
    const endBrand = (page) * BRANDS_PER_PAGE;
    this.setState({ viewBrands: this.props.propsBrands.slice(startBrand, endBrand) });  //step 11
  }
  /* Filter */
  updateFilterKeyword(value) {
    if (!value) {
      this.updateBrandsByPage(this.state.currentPage);
    }
    this.setState({ keyword: value });
  }
  filterByKeyword() {
    const keyword = this.state.keyword.toLowerCase();
    const keyvalue = keyword.trim();
    if (!keyvalue.length) {
      return;
    }
    const selectedBrands = this.props.propsBrands.filter(brand => {
      for (const industry of brand.industries) {
        if (industry.toLowerCase().includes(keyvalue)) {
          return true;
        }
      }
      for (const country of brand.headquarter_countries) {
        if (country.toLowerCase().includes(keyvalue)) {
          return true;
        }
      }
      if (brand.name.toLowerCase().includes(keyvalue)) {
        return true;
      }
      return false;
    })
    this.setState({ viewBrands: selectedBrands });
  }
  filterByKeywordOnPress(key) {
    const keyword = this.state.keyword.toLowerCase();
    if (key === 'Enter') {
      if (!keyword) {
        return;
      } else {
        this.filterByKeyword();
      }
    }
  }
  get FilterBar() {
    return (
      <Grid container direction='row' justify='center' alignItems='center'
        style={{ marginTop: '45px', marginBottom: '10PX' }}>
        <TextField label='Keyword' variant='outlined' value={this.state.keyword}
          onChange={event => this.updateFilterKeyword(event.target.value)}
          onKeyDown={event => this.filterByKeywordOnPress(event.key)} />
        <Button variant='contained' color='primary' style={{ marginLeft: '5px' }}
          onClick={() => this.filterByKeyword()}>
          Search
       </Button>
      </Grid>
    )
  }

  /* Brands */
  redirectToDetail(id) {
    this.props.propsFetchBrand(id).then(() => {
      this.props.history.push(`/brand/${id}`);
    });
  }
  get NotFound() {
    return (
      'Loading...'
    );
  }
  get Brands() {
    const brands = this.state.viewBrands;  //step 11
    if (!brands.length) {
      return this.NotFound;
    }

    const BrandDOMs = [];
    for (let i = 0; i < brands.length; i++) {
      const brand = brands[i];
      const logoUrl = brand.logo_url;
      const brandId = brand.id;
      const classes = this.props.classes;
      const isXs = this.props.width === 'xs';
      let imageStyle = {
        width: '100%'
      };
      if (isXs) {
        imageStyle = {
          height: '100%'
        };
      }
      
      BrandDOMs.push(
        <Grid item xs={12} sm={3} md={2} key={i}>
          <Card elevation={3} className={classes.card}
            onClick={() => this.redirectToDetail(brandId)}>
            {/* <CardMedia image={logoUrl} /> */}
            <CardContent>
              <Grid container direction='row' justify='center' alignItems='center'
                style={{
                  height: '200px',
                  marginBottom: '1px',
                  textAlign: 'center'
                }}>
                <img style={imageStyle} src={logoUrl} alt='logo' />
              </Grid>
            </CardContent>
          </Card>
        </Grid>
      );
    }
    return BrandDOMs;
  }

  /* Pagination*/
  getNextPage() {
    if (this.state.currentPage < this.state.lastPage) {
      const nextPageNumber = this.state.currentPage + 1;
      this.updateBrandsByPage(nextPageNumber);
      this.setState({ currentPage: nextPageNumber });
    }
  }
  getPrevPage() {
    if (this.state.currentPage > this.state.firstPage) {
      const prevPageNumber = this.state.currentPage - 1;
      this.updateBrandsByPage(prevPageNumber);
      this.setState({ currentPage: prevPageNumber });
    }
  }
  getFirstPage() {
    if (this.state.currentPage !== this.state.firstPage) {
      this.updateBrandsByPage(this.state.firstPage);
      this.setState({ currentPage: this.state.firstPage });
    }
  }
  getLastPage() {
    if (this.state.currentPage !== this.state.lastPage) {
      this.updateBrandsByPage(this.state.lastPage);
      this.setState({ currentPage: this.state.lastPage });
    }
  }
  get DisplayPages() {
    const currentPage = this.state.currentPage;
    const firstPage = this.state.firstPage;
    const lastPage = this.state.lastPage;
    const showPageCounts = 5;
    const halfUnit = Math.floor(showPageCounts / 2);
    let pageNumbers = [];
    if (lastPage > showPageCounts) {
      if (currentPage <= (halfUnit + firstPage)) {
        for (let pageNumber = firstPage; pageNumber < firstPage + showPageCounts; pageNumber++) {
          pageNumbers.push(pageNumber);
        }
      } else if (currentPage >= (lastPage - halfUnit)) {
        for (let pageNumber = lastPage; pageNumber > lastPage - showPageCounts; pageNumber--) {
          pageNumbers.unshift(pageNumber);
        }
      } else {
        const startIndex = currentPage - halfUnit;
        for (let pageNumber = startIndex; pageNumber < startIndex + showPageCounts; pageNumber++) {
          pageNumbers.push(pageNumber);
        }
      }
    } else {
      for (let pageNumber = firstPage; pageNumber < firstPage + lastPage; pageNumber++) {
        pageNumbers.push(pageNumber);
      }
    }

    const displayPages = []
    for (let pageNumber of pageNumbers) {
      displayPages.push(
        <Typography key={pageNumber} variant='body2' style={{
          cursor: 'pointer', margin: '0 6px',
          color: pageNumber === this.state.currentPage ? 'blue' : 'black',
          fontWeight: pageNumber === this.state.currentPage ? 'bold' : 'inherit'
        }}
          onClick={() => {
            this.updateBrandsByPage(pageNumber);
            this.setState({ currentPage: pageNumber });
          }}>
          {pageNumber}
        </Typography>
      )
    }
    return displayPages;
  }
  get PaginationBar() {
    const keyvalue = this.state.keyword.trim();
    if (keyvalue.length) {  //not display paginationBar if there are information in filter
      return '';
    }
    const brands = this.state.viewBrands;
    if (!brands.length) {    //not display paginationBar before get the data
      return '';
    }
    return (
      <Grid container direction='row' justify='center' alignItems='center'>
        <Typography variant='h6' style={{
          cursor: 'pointer', margin: '0 6px',
          color: this.state.currentPage === this.state.firstPage ? 'lightgrey' : 'black'
        }}
          onClick={() => this.getFirstPage()}>
          {'<<'}
        </Typography>
        <Typography variant='h6' style={{
          cursor: 'pointer', margin: '0 6px',
          color: this.state.currentPage === this.state.firstPage ? 'lightgrey' : 'black'
        }}
          onClick={() => this.getPrevPage()}>
          {'<'}
        </Typography>
        {this.DisplayPages}
        <Typography variant='h6' style={{
          cursor: 'pointer', margin: '0 6px',
          color: this.state.currentPage === this.state.lastPage ? 'lightgrey' : 'black'
        }}
          onClick={() => this.getNextPage()}>
          {'>'}
        </Typography>
        <Typography variant='h6' style={{
          cursor: 'pointer', margin: '0 6px',
          color: this.state.currentPage === this.state.lastPage ? 'lightgrey' : 'black'
        }}
          onClick={() => this.getLastPage()}>
          {'>>'}
        </Typography>
      </Grid>
    );
  }

  render() {
    const isXs = this.props.width === 'xs';
    const isSm = this.props.width === 'sm';
    let maxWidth = '1200px';
    if (isSm) {
      maxWidth = '750px';
    } else if (isXs) {
      maxWidth = '360px';
    }
    return (
      <Grid container direction='column'>
        {/** ====== Filter bar */}
        {this.FilterBar}
        {/* ====== Brands */}
        <Grid item>
          <Grid container spacing={3}
            style={{
              margin: '12px auto 0', width: '100%', maxWidth: maxWidth
            }} >
            {this.Brands}
          </Grid>
        </Grid>
        {/* ====== Pagination Bar */}
        <Grid item >
          {this.PaginationBar}
        </Grid>
      </Grid>
    )
  }
}

const mapStateToProps = state => {    //step 10
  const brands = state.brandReducer.brands;
  return { propsBrands: brands.slice() };
}

const mapDispatchToProps = {    //mapDispatchToProps as a Object
  propsFetchBrands: () => fetchBrands(),   // step 2
  propsFetchBrand: (brandId) => fetchBrand(brandId),
}

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(
    withStyles(STYLE)(withWidth()(BrandGallery)))
);  