const isProduction = true;
const PROD_URL = "https://brand-insights-api.herokuapp.com";
const DEV_URL = "http://localhost:5000";
const BASE_URL = isProduction ? PROD_URL : DEV_URL;

export const getBrands = () => {
    const url = `${BASE_URL}/api/brands`;
    return fetch(url, {     // l3-l8: step 4
        method: 'GET',
        mode: 'cors',
        headers: {
            'Content-Type': 'application/json'
        }
    }).then(brands => {
        return brands.json();
    });       //step 5
};

export const getBrand = brandId => {
    const url = `${BASE_URL}/api/brand/${brandId}`;
    return fetch(url, {
        method: 'GET',
        mode: 'cors',
        headers: {
            'Content-Type': 'application/json'
        }
    }).then(brand => {
        return brand.json();
    });
};

export const postBrand = brand => {
    const url = `${BASE_URL}/api/brand`;
    return fetch(url, {
        method: 'POST',
        body: JSON.stringify(brand),
        mode: 'cors',
        headers: {
            'Content-Type': 'application/json'
        }
    }).then(brandId => {
        return brandId.json();
    });
}

