import React from 'react';
/** Components **/
import { AppBar, Toolbar, Typography, IconButton, Grid, Tooltip } from '@material-ui/core';
import { faPhone } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** Constants **/
import {NAVIGATION_BAR} from '../../constants/properties';

class NavigationBar extends React.Component {
    /** Media Icon information. */
    get mediaIcons() {
        const iconBar = [];
        for (let i = 0; i < NAVIGATION_BAR.iconsInfo.length; i++) {
            const iconInfo = NAVIGATION_BAR.iconsInfo[i]
            iconBar.push(
                <IconButton
                    color="inherit"
                    aria-label="contact"
                    href={iconInfo.link}
                    key={i}
                    target="_blank">
                    <FontAwesomeIcon icon={iconInfo.icon} size="1x" />
                </IconButton>
            )
        }

        return iconBar;
    }

    /** Phone information. */
    get phoneInfo() {
        return (
            <Tooltip title="Contact: 1-508-309-2110" placement="bottom">
                <IconButton
                    color="inherit"
                    aria-label="call">
                    <FontAwesomeIcon icon={faPhone} size="xs" />
                </IconButton>
            </Tooltip>
        )
    }

    render() {
        return (            
            <AppBar position="static" elevation={0}>
                <Toolbar>
                    <Grid
                        container 
                        direction="row"
                        justify="space-between"
                        alignItems="center"
                        style={{height: '85px'}}
                    >
                        <Typography variant="h6">Glogou</Typography>
                        <Grid item>
                            {this.phoneInfo}
                            {this.mediaIcons}
                        </Grid> 
                    </Grid>
                </Toolbar>
            </AppBar>
        );
    }
}

export default NavigationBar;