import React from 'react';
import { connect } from 'react-redux';
import { fetchBrand } from '../../dispatchers/dispatcher';
import { withWidth, Grid, Typography, Button, Card, CardContent } from '@material-ui/core'
import { withStyles } from '@material-ui/styles';
import ROUTING_PATHS from '../../constants/routes';
import { ARTICLES } from '../../constants/mock';
import { INFO_DISPLAY } from '../../constants/properties';

const STYLE = () => ({
    card: {
        background: '#f5f5f5',
        cursor: 'pointer',
        '&:hover': { boxShadow: '0 3px 13px #666' }
    }
})

class BrandDetail extends React.Component {
    state = { id: -1 }
    static getDerivedStateFromProps(props, state) {
        const brandId = props.match.params.id;
        if (state.id !== brandId) {
            state.id = brandId;
            props.propsFetchBrand(brandId);
        }
        return null;
    }
    // componentDidMount(){
    //     const pathInfo=this.props.location.pathname.split(`/`);
    //     const brandId=pathInfo.pop();
    //     this.props.propsFetchBrand(brandId);
    // }

    get Banner() {
        const mission = this.props.propsBrand.mission;
        return (
            <Grid container direction='row'
                justify='center' alignItems='center'
                style={{
                    background: '#1a237e',
                    padding: '24px',
                }}>
                <Typography variant='h6'
                    style={{
                        color: '#e8eaf6',
                        maxWidth: '1200px'
                    }}>
                    {mission}
                </Typography>
            </Grid>
        );
    }

    redirect(path) {
        this.props.history.push(path);
    }

    get LinksBar() {
        const isXs = this.props.width === 'xs';
        return (
            <Card elevation={0}>
                <Grid container direction='column' style={{
                    padding: '16px',
                    width: isXs ? '300px' : 'auto',
                    margin: isXs ? '0 auto' : 'auto'
                }}>
                    <Button color='primary' variant='contained'
                        onClick={() => this.redirect(ROUTING_PATHS.BRAND_GALLERY)}
                        style={{
                            borderRadius: '20px'
                        }}>
                        Gallery
                    </Button>
                    <Button color='primary' variant='contained'
                        onClick={() => this.redirect(ROUTING_PATHS.ADD_PORTFOLIO)}
                        style={{
                            marginTop: '8px',
                            borderRadius: '20px'
                        }}>
                        Join
                    </Button>
                    <Button color='primary' variant='contained'
                        onClick={() => this.redirect(ROUTING_PATHS.INSIGHTS)}
                        style={{
                            marginTop: '8px',
                            borderRadius: '20px'
                        }}>
                        Insights
                    </Button>
                </Grid>
            </Card >
        );
    }

    get BrandLogo() {
        const logoUrl = this.props.propsBrand.logo_url;
        const isXs = this.props.width === 'xs';
        let imageStyle = { width: '100%' };
        if (isXs) {
            imageStyle = { height: '100%' };
        }
        if (!logoUrl) {
            return '';
        }
        return (
            <Card style={{ marginTop: '16px' }}>
                <CardContent>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center"
                        style={{
                            height: '200px',
                            marginBottom: '8px',
                            textAlign: 'center'
                        }}>
                        <img style={imageStyle} src={logoUrl} alt="logo" />
                    </Grid>
                </CardContent>
            </Card>
        );
    }

    get BasicInformation() {
        const description = this.props.propsBrand.description;
        const InfoType = [];
        if (!description) {
            return '';
        }
        for (let i = 0; i < INFO_DISPLAY.length; i++) {
            const infoDisplay = INFO_DISPLAY[i];
            const title = infoDisplay.title;
            const content=  infoDisplay.content;

            // Avoid the initial props does not have anything.
            // if (!this.props.propsBrand[content]) {
            //     return '';
            // }
            InfoType.push(
                <Grid container direction='row' spacing={1} key={i}>
                    <Grid item md={3} sm={5} xs={4}>
                        <Grid container direction="column" alignItems="flex-end">
                            <Typography variant='body1' style={{
                                color: '#1a237e', fontWeight: 'bold',
                                padding: '7px', borderRadius: '4px'
                            }}>
                                {title}:
                                </Typography>
                        </Grid>
                    </Grid>
                    <Grid item md={8} sm={6} xs={7}>
                        <Grid container direction="column" alignItems="flex-start">
                            <Typography variant='body1' gutterBottom style={{ padding: '7px' }}>
                                {['key_people','industries','products'].includes(content) ?
                                    this.props.propsBrand[content].join(', ')
                                    : this.props.propsBrand[content]}
                            </Typography>
                        </Grid>
                    </Grid>
                </Grid>
            );
        };
        return (
            <Grid container direction='column' >
                <div>
                    {InfoType}
                    <Typography variant='body1' style={{ marginTop: '15px' }}>
                        {description}
                    </Typography>
                </div>
            </Grid>
        )
    }

    redirectionToArticle(link) {
        window.location.assign(link);
    }

    get Articles() {
        const brandName = this.props.propsBrand.name;
        const brandArticles = ARTICLES[brandName];
        const css = this.props.classes;
        if (!brandArticles || !brandArticles.length) {
            return '';
        }
        const articles = [];
        for (const article of brandArticles) {
            articles.push(
                <Grid item key={article.title} style={{ margin: '16px 0' }}>
                    <Card elevation={2} className={css.card} onClick={() => this.redirectionToArticle(article.link)}
                        style={{ padding: '16px' }}>
                        <Grid container direction='column'>
                            <Typography variant='h6'>
                                {article.title}
                            </Typography>
                            <Typography variant='overline' style={{ color: '#0d47a1', fontWeight: 'bold' }}>
                                {new Date().toDateString()}
                            </Typography>
                            <Typography variant='body1'>
                                {article.summary}
                            </Typography>
                        </Grid>
                    </Card>
                </Grid>
            );
        }
        return (
            <Grid container direction='column'>
                {articles}
            </Grid>
        );
    }

    render() {
        const isXs = this.props.width === 'xs';
        return (
            <Grid container direction='column'>
                {/** ====== draw a color line*/}
                <Grid item style={{ background: '#cfd8dc', padding: '3px' }}></Grid>
                {/** ====== Banner */}
                <Grid item>
                    {this.Banner}
                </Grid>
                {/** ====== Brand Information */}
                <Grid item>
                    <Grid container direction={isXs ? 'column-reverse' : 'row'} justify='space-between'
                        style={{
                            width: '100%',
                            maxWidth: '1200px',
                            margin: '0 auto'
                        }}>
                        <Grid item sm={7} style={{ padding: '16px' }}>
                            {this.BasicInformation}
                            {/** ====== Brand Articles */}
                            {this.Articles}
                        </Grid>
                        <Grid item sm={3} style={{ padding: '16px' }}>
                            {this.LinksBar}
                            {this.BrandLogo}
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        );
    }
}

const mapStateToProps = state => {
    const brand = state.brandReducer.brand;
    return {
        propsBrand: brand
    };
}

const mapDispatchToProps = dispatch => {
    return {
        propsFetchBrand: (brandId) => dispatch(fetchBrand(brandId)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(withWidth()(withStyles(STYLE)(BrandDetail)));