/** connection */
import { getBrands, getBrand, postBrand } from '../utils/util';
import { updateBrandsOnStore, updateBrandOnStore } from '../actions/action';
//import store from '../reducers/store';

export const fetchBrands = () => {
    return actionDispatch => {
        // call util
        return getBrands().then(brands => {   // then: step 3 & 6
            // update with dispatcher  step 7
            actionDispatch(updateBrandsOnStore(brands));
        }).catch(error => {
            //handle error
        });        //get the brands
    }
};

export const fetchBrand = brandId => {
    return actionDispatch => {
        return getBrand(brandId).then(brand => {
            actionDispatch(updateBrandOnStore(brand));
        });
    }
};

export const addBrand = brand => {
    return () => {
        return postBrand(brand).then(brandId => {
            return brandId;
        });
    }
}