import React from 'react';
/** Import route module **/
import { Switch, Route, Redirect } from 'react-router-dom';
/** Import components **/
import Home from '../home/Home';
import BrandDetail from '../brand-detail/BrandDetail';
/** Import types */
import ROUTING_PATHS from '../../constants/routes';

/** Integrated routing component for controlling the project route switch.*/
class Routing extends React.Component {
    render() {
        return (
            <Switch>
                <Redirect from={ROUTING_PATHS.ROOT} to={ROUTING_PATHS.BRAND_GALLERY} exact />
                <Route path={ROUTING_PATHS.BRAND_GALLERY} component={Home} exact />
                <Route path={ROUTING_PATHS.ADD_PORTFOLIO} component={Home} exact />
                <Route path={ROUTING_PATHS.INSIGHTS} component={Home} exact />
                <Route path={ROUTING_PATHS.BRAND_DETAIL} component={BrandDetail} exact />
            </Switch>
        )
    }
}

export default Routing;